@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-container {
  flex-grow: 1;
}
.title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
.title--basic {
  color: #000000;
}
.title--additional {
  color: #ffffff;
}
.description {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
}
.description--basic {
  color: #000000;
}
.description--additional {
  color: #ffffff;
}
.button {
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.page {
  height: 100%;
}
.page__body {
  display: flex;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  background-color: #e7e5e5;
  width: 100%;
  min-height: 100%;
  margin: 0;
}
.page__wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-height: 100%;
  max-width: 1200px;
  min-width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
.main-header {
  position: relative;
  min-height: 56px;
}
.main-header__logo-image {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
}
@media (min-width: 768px) {
  .main-header__logo-image {
    z-index: 2;
    display: block;
    height: 96px;
    margin: 0 auto;
  }
}
.main-navigation {
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-navigation {
    top: 0;
    z-index: 1;
    margin: 0 auto;
    background-color: #ffffff;
  }
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
.main-navigation--nojs .main-navigation__list {
  position: static;
  min-height: 0;
}
.main-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  min-width: 320px;
}
@media (min-width: 768px) {
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .main-navigation__list {
    max-width: 1200px;
  }
}
.main-navigation--closed {
  top: 0;
  margin-top: 0;
}
.main-navigation--closed .main-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  min-width: 320px;
}
@media (min-width: 768px) {
  .main-navigation--closed .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .main-navigation--closed .main-navigation__list {
    max-width: 1200px;
  }
}
@media (max-width: 767px) {
  .main-navigation--nojs,
  .main-navigation--opened {
    position: relative;
  }
}
@media (max-width: 767px) {
  .main-navigation--optional {
    top: 0;
    margin-top: 0;
  }
}
.main-navigation--opened .main-navigation__list {
  position: absolute;
  top: -3px;
  left: 0;
  width: 100%;
  background: #81b3d2;
}
@media (min-width: 768px) {
  .main-navigation--opened .main-navigation__list {
    background: #ffffff;
  }
}
.main-navigation__item {
  box-sizing: border-box;
  margin: 0.5px 0;
  border-bottom: 1px solid rgba(129, 179, 210, 0.2);
  border-top: 1px solid rgba(1, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .main-navigation__item {
    width: 20%;
    margin: 0;
    box-shadow: none;
    border: none;
  }
}
.main-navigation__item:nth-child(1) {
  border-top: none;
}
@media (min-width: 768px) {
  .main-navigation__item:nth-child(2) {
    margin-right: 20%;
  }
}
.main-navigation__item:last-child::after {
  content: "";
  position: relative;
  top: 1px;
  display: block;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-navigation__item:last-child::after {
    content: none;
  }
}
.main-navigation--closed .main-navigation__item {
  display: none;
}
@media (min-width: 768px) {
  .main-navigation--closed .main-navigation__item {
    display: block;
  }
}
.main-navigation__link {
  display: block;
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  background-color: #81b3d2;
  padding: 18px 20px 10px 20px;
}
@media (min-width: 768px) {
  .main-navigation__link {
    color: #000000;
    font-size: 14px;
    background-color: #ffffff;
    padding: 17px 0 12px 0;
  }
}
.main-navigation__item:last-child .main-navigation__link {
  padding-bottom: 4px;
}
@media (min-width: 768px) {
  .main-navigation__item:last-child .main-navigation__link {
    padding: 17px 0 12px 0;
  }
}
@media (min-width: 768px) {
  .main-navigation__link--current {
    color: #766357;
  }
}
.main-navigation__link:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .main-navigation__link:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
}
.main-navigation__link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-navigation__link:active {
    background-color: #ffffff;
    color: rgba(0, 0, 0, 0.3);
  }
}
.main-navigation__toggle {
  position: absolute;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 51px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation--closed .main-navigation__toggle {
  top: auto;
}
.main-navigation--opened .main-navigation__toggle {
  --color: #ffffff;
  top: 0;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 29px;
  left: 25px;
  width: 28px;
  height: 2px;
  background-color: var(--color);
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle:hover::before,
.main-navigation__toggle:hover::after {
  --color: rgba(255, 255, 255, 0.6);
}
.main-navigation--opened .main-navigation__toggle:active::before,
.main-navigation__toggle:active::after {
  --color: rgba(255, 255, 255, 0.3);
}
.main-navigation--closed .main-navigation__toggle::before {
  --color: #81b3d2;
  content: "";
  position: absolute;
  top: 16px;
  left: 26px;
  width: 24px;
  height: 4px;
  background-color: var(--color);
  box-shadow: 0 9px 0 0 var(--color), 0 18px 0 0 var(--color);
}
.main-navigation--closed .main-navigation__toggle:hover::before {
  --color: rgba(129, 179, 210, 0.6);
}
.main-navigation--closed .main-navigation__toggle:active::before {
  --color: rgba(129, 179, 210, 0.3);
}
