.main-navigation {
  position: relative;
  z-index: 2;

  @media (min-width: @tablet-width) {
    top: 0;
    z-index: 1;
    margin: 0 auto;
    background-color: @basic-color;
  }
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-navigation--nojs .main-navigation__list {
  position: static;
  min-height: 0;
}

.main-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  min-width: 320px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    max-width: @desktop-width;
  }
}

.main-navigation--closed {
  top: 0;
  margin-top: 0;
}

.main-navigation--closed .main-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  min-width: 320px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    max-width: @desktop-width;
  }
}

.main-navigation--nojs,
.main-navigation--opened {
  @media (max-width: @mobile-width-only) {
    position: relative;
  }
}

.main-navigation--optional {
  @media (max-width: @mobile-width-only) {
    top: 0;
    margin-top: 0;
  }
}

.main-navigation--opened .main-navigation__list {
  position: absolute;
  top: -3px;
  left: 0;
  width: 100%;
  background: @primary-color;

  @media (min-width: @tablet-width) {
    background: @basic-color;
  }
}

.main-navigation__item {
  box-sizing: border-box;
  margin: 0.5px 0;
  border-bottom: 1px solid rgba(@primary-color, 0.2);
  border-top: 1px solid rgba(1, 1, 1, 0.2);

  @media (min-width: @tablet-width) {
    width: 20%;
    margin: 0;
    box-shadow: none;
    border: none;
  }
}

.main-navigation__item:nth-child(1) {
  border-top: none;
}

.main-navigation__item:nth-child(2) {
  @media (min-width: @tablet-width) {
    margin-right: 20%;
  }
}

.main-navigation__item:last-child::after {
  content: "";
  position: relative;
  top: 1px;
  display: block;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);

  @media (min-width: @tablet-width) {
    content: none;
  }
}

.main-navigation--closed .main-navigation__item {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}

.main-navigation__link {
  display: block;
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: @basic-color;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  background-color: @primary-color;
  padding: 18px 20px 10px 20px;

  @media (min-width: @tablet-width) {
    color: @contrasting-color;
    font-size: 14px;
    background-color: @basic-color;
    padding: 17px 0 12px 0;
  }
}

.main-navigation__item:last-child .main-navigation__link {
  padding-bottom: 4px;

  @media (min-width: @tablet-width) {
    padding: 17px 0 12px 0;
  }
}

.main-navigation__link--current {
  @media (min-width: @tablet-width) {
    color: #766357;
  }
}

.main-navigation__link:hover {
  background-color: #669ec0;

  @media (min-width: @tablet-width) {
    background-color: @basic-color;
    color: @primary-color;
  }
}

.main-navigation__link:active {
  background-color: #5496bd;
  color: rgba(@basic-color, 0.3);

  @media (min-width: @tablet-width) {
    background-color: @basic-color;
    color: rgba(0, 0, 0, 0.3);
  }
}

.main-navigation__toggle {
  position: absolute;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 51px;
  background-color: transparent;
  cursor: pointer;
  border: none;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-navigation--closed .main-navigation__toggle {
  top: auto;
}

.main-navigation--opened .main-navigation__toggle {
  --color: @basic-color;
  top: 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 29px;
    left: 25px;
    width: 28px;
    height: 2px;
    background-color: var(--color);
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-navigation--opened .main-navigation__toggle:hover::before,
.main-navigation__toggle:hover::after {
  --color: rgba(@basic-color, 0.6);
}

.main-navigation--opened .main-navigation__toggle:active::before,
.main-navigation__toggle:active::after {
  --color: rgba(@basic-color, 0.3);
}

.main-navigation--closed .main-navigation__toggle::before {
  --color: @primary-color;
  content: "";
  position: absolute;
  top: 16px;
  left: 26px;
  width: 24px;
  height: 4px;
  background-color: var(--color);
  box-shadow: 0 9px 0 0 var(--color), 0 18px 0 0 var(--color);
}

.main-navigation--closed .main-navigation__toggle:hover::before {
  --color: rgba(@primary-color, 0.6);
}

.main-navigation--closed .main-navigation__toggle:active::before {
  --color: rgba(@primary-color, 0.3);
}
